import React, { Component } from 'react';

import Highlight from 'react-highlight'
import 'highlight.js/styles/dracula.css';

class Cv extends Component {
    render() {
        return (
            <Highlight className='typescript'>
                {`
                        interface Education {
                            name: string,
                            degree: string,
                            specialization: string,
                            dates: Object
                        }

                        interface WorkExperience {
                            position: string,
                            companyName: string,
                            dates: Object
                        }

                        class Cv {
                            public name: string = 'Vladislav Semenets';
                            public title: string = 'Front-end developer && Software Engineer';
                            public address: string = 'Ukraine, Zaporozhye';
                            public currentJob: string = 'Front-end developer – AMgrade | Technisches Büro';
                            public description: string = 'Versatile Front-end Developer with 3+ years of experience.';
                            public email: string = 'developerdev95@gmail.com';

                            public isOpenToJobOffers: boolean = false;

                            public workExperience: Array<WorkExperience> = [
                                {
                                    position: 'Front-end developer',
                                    companyName: 'AMgrade | Technisches Büro',
                                    dates: {
                                        start: 'July 2016',
                                        end: 'present time'
                                    }
                                },
                                {
                                    position: 'Full-stack Developer',
                                    companyName: 'IT Master',
                                    dates: {
                                        start: 'June 2014',
                                        end: 'July 2016'
                                    }
                                },
                                {
                                    position: 'Full-stack Developer',
                                    companyName: 'VclubeUa',
                                    dates: {
                                        start: 'June 2013',
                                        end: 'June 2014'
                                    }
                                }
                            ];

                            public education: Array<Education> = [
                                {
                                    name: 'Zaporizhzhya National Technical University',
                                    degree: 'Higher education',
                                    specialization: 'Software Engineering',
                                    dates: {
                                        start: '2014',
                                        end: '2017'
                                    }
                                },
                                {
                                    name: 'Zaporizhzhya Electrotechnical College',
                                    degree: 'Secondary technical',
                                    specialization: 'Software development',
                                    dates: {
                                        start: '2010',
                                        end: '2014'
                                    }
                                }
                            ];
                        }
                                        `}
            </Highlight>
        );
    }
}

export default Cv;



